\n          label: \"否\",\r\n        },\r\n      ],\r\n      initBeginTime: \"\",\r\n      initEndTime: \"\",\r\n\r\n      queryParams: {\r\n        isksjk: \"\",\r\n        device: \"\",\r\n        intervalMinutes: \"\",\r\n        beginTime: \"\",\r\n        endTime: \"\",\r\n      },\r\n      dataTable: {\r\n        pageSize: 50,\r\n        pageSizes: [10, 20, 50, 100, 1000, 5000, 10000, 20000],\r\n        url: \"/web/v3/market/queryH5\",\r\n        emptyText: \"没有记录\",\r\n        showSummary: true,\r\n        summaryMethod: this.getSummaries,\r\n      },\r\n      hiddenColumns: [],\r\n      columnsOptions: [\r\n        {\r\n          value: \"date\",\r\n          label: \"date\",\r\n        },\r\n        {\r\n          value: \"plat\",\r\n          label: \"plat\",\r\n        },\r\n        {\r\n          value: \"ksjk_existing\",\r\n          label: \"ksjk_existing\",\r\n        },\r\n        {\r\n          value: \"uv\",\r\n          label: \"uv\",\r\n        },\r\n        {\r\n          value: \"phones_count\",\r\n          label: \"phones_count\",\r\n        },\r\n        {\r\n          value: \"register_count\",\r\n          label: \"register_count\",\r\n        },\r\n        {\r\n          value: \"activation_count\",\r\n          label: \"activation_count\",\r\n        },\r\n        {\r\n          value: \"jinjian\",\r\n          label: \"jinjian\",\r\n        },\r\n        {\r\n          value: \"approval\",\r\n          label: \"approval\",\r\n        },\r\n        {\r\n          value: \"emitCn\",\r\n          label: \"emitCn\",\r\n        },\r\n        {\r\n          value: \"emitMoney\",\r\n          label: \"emitMoney\",\r\n        },\r\n        {\r\n          value: \"act_reg_rate\",\r\n          label: \"act_reg_rate\",\r\n        },\r\n        {\r\n          value: \"jj_jh_rate\",\r\n          label: \"jj_jh_rate\",\r\n        },\r\n        {\r\n          value: \"review_jinj_rate\",\r\n          label: \"review_jinj_rate\",\r\n        },\r\n     